.hero {
    display: flex;
    justify-content: center;
    align-items: center;
    background-color: #f5f5f5;
    height: 80vh;
  }
  
  .hero-content {
    text-align: center;
  }
  
  .hero-content h1 {
    font-size: 3rem;
    color: #333;
    margin-bottom: 1rem;
  }
  
  .hero-content p {
    font-size: 1.5rem;
    color: #666;
  }
  
  .hero-image {
    margin-left: 2rem;
    max-width: 50%;
  }
  
  .hero-image img {
    display: block;
    width: 100%;
    height: auto;
  }
  
  @media screen and (max-width: 768px) {
    .hero {
      height: auto;
      flex-direction: column;
      justify-content: flex-start;
      padding-top: 2rem;
    }
    
    .hero-image {
      margin: 2rem auto 0;
      max-width: 100%;
    }
    
    .hero-content h1 {
      font-size: 2rem;
    }
    
    .hero-content p {
      font-size: 1rem;
    }
  }
  